import React from 'react';
import PersonService from './services/PersonService';

const PersonForm = ({
  persons,
  setNewName,
  newName,
  setNumber,
  newNumber,
  setPersons,
  setMessage,
  setStatus,
}) => {
  const newPerson = async(e) => {
    debugger;
    e.preventDefault();

    const person = {
      name: newName,
      number: newNumber,
    };

    if (newName.length < 1 && newNumber.length < 1) {
      setMessage('Name or number missing');
      setStatus('error');

    } else {


      const checkDuplicates = persons.filter((p) => p.name === person.name);

      if (checkDuplicates.length === 0 || checkDuplicates === undefined) {
        PersonService.createPerson(person)
          
          
          .then(PersonService.getPersons())
          .then((person) => setPersons(persons.concat(person)))
          .then(setMessage(`${person.name} was added`), setStatus('success')
          ).then(setNewName(''), setNumber(''),
            setTimeout(() => {
              setMessage(null)
            }, 5000));
      } else {
        const duplicateDialog = window.confirm(
          `${person.name} already exists, add new number?`
        );

        if (duplicateDialog) {
          PersonService.updatePerson(checkDuplicates[0].id, person)
            .then((response) => {
              setPersons(
                persons.map((p) =>
                  p.id !== checkDuplicates[0].id ? p : response
                )
              );
            })
            .then(setNewName(''), setNumber(''));
        }
      }
    }
  };

  const handleChangePerson = (e) => {
    setNewName(e.target.value);
  };

  const handleNumber = (e) => {
    e.preventDefault();
    setNumber(e.target.value.trim());
  };

  return (
    <form onSubmit={newPerson}>
      <div>
        name: <input onChange={(e) => handleChangePerson(e)} value={newName} />
      </div>

      <div>
        number: <input onChange={(e) => handleNumber(e)} value={newNumber} />
      </div>
      <button type="submit">add</button>
      <div>debug: {newName}</div>
    </form>
  );
};

export default PersonForm;
